.table-bus {
  .table {
    thead {
      tr {
        th {
          &:nth-child(7) {
            text-align: center;
          }
        }
      }
    }
  }
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.customTitle{
  font-size: 20px;
}