.action-center-container {
  margin: -30px -30px 30px; }
  .action-center-container .action-center {
    display: flex;
    justify-content: space-between;
    background: rgba(255, 255, 255, 0.55);
    padding: 30px;
    position: relative;
    align-content: center; }
    .action-center-container .action-center > div {
      display: flex; }
      .action-center-container .action-center > div .action-btn i {
        font-weight: 600; }
      .action-center-container .action-center > div.actions {
        display: flex;
        justify-content: flex-end; }
    .action-center-container .action-center.contracted .actions {
      flex-direction: column-reverse;
      align-items: flex-end; }
      .action-center-container .action-center.contracted .actions > div:not(:first-child) {
        margin-bottom: 10px; }
      .action-center-container .action-center.contracted .actions > div .router-link button {
        margin: 0 !important; }

